.number{
  color : red;
}

.App{
  margin: 0 auto;
  margin-top: 200px;
  text-align: center;
  width: 400px;
}

.chat-window{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 80vh;
  width: 60%;
  max-width: 800px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  padding: 16px;
}

.chat-messages{
  display: flex;
  flex-direction: column;
  flex: 1;
  overflow-y: auto;
  padding: 8px;
}

.chat-message{
  margin-bottom: 8px;
}

.user-message{
  align-self: flex-end;
  background-color: #d2ebff;
  border-radius: 10px;
  padding: 8px;
  max-width: 60%;
}

.bot-message{
  align-self: flex-start;
  background-color: #f1f1f1;
  border-radius: 10px;
  padding: 8px;
  max-width: 60%;
}

.chat-input{
  display: flex;
  width: 100%;
  margin-top: 16px;
}

.chat-input input{
  flex: 1;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 5px;
  outline: none;
}

.chat-input button{
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  padding: 8px 16px;
  margin-left: 8px;
  cursor: pointer;
  transition: background-color 0.2s;
}

.chat-input button:hover{
  background-color: #0056b3;
}

.clearfix::after{
  content: "";
  display: table;
  clear: both;
}

body {
  background-color: #f4f4f4;  /* 예시 배경색. 원하는 색상으로 변경 가능 */
  font-family: 'Arial', sans-serif;  /* 예시 폰트. 원하는 폰트로 변경 가능 */
}
